export default {
  components: {
  },
  data () {
    return {
  		num: 1
    }
  },
  created () {
  },
  methods: {    
    router (path) {
      this.$router.push(path)
    },
    jian () {
    	if(this.num <= 1) {
    		return 
    	}
    	this.num--
    },
    jia () {
    	this.num++
    }
  }
}



