
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
Group,
Popup
},
data () {
return {
show: false,
tabIndex: 0
}
},
created () {

},
methods: {
router (path) {
this.$router.push(path)
},
tranShow (index) {
this.tabIndex = index
this.show = !this.show
},
close () {
this.tabIndex = 0
}
}
}



