
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
components: {
Tab,
TabItem
},
data () {
return {
tabIndex: 1
}
},
created () {

},
methods: {
router (path) {
this.$router.push(path)
},
onItemClick (index) {
this.tabIndex = index
}
}
}



